import Vue from 'vue'
import VueRouter from 'vue-router'

import Login from '../views/Login.vue'
import Home from '../views/Home.vue'
import Store from '../views/Store.vue'
import StoreHome from '../views/Store/StoreHome.vue'
import MyStore from '../views/Store/MyStore.vue'
import AddStore from '../views/Store/AddStore.vue'


Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'login',
    component: Login
  },
  {
    path: '/home',
    name: 'home',
    component: Home,
  },
  {
    path: '/store',
    name: 'store',
    component: Store,
    children: [
      {
        path: 'storeHome',
        name: 'storeHome',
        component: StoreHome,
      },
      {
        path: 'myStore',
        name: 'myStore',
        component: MyStore,
      },
      {
        path: 'addStore',
        name: 'addStore',
        component: AddStore,
      }
    ]
  },
]

const router = new VueRouter({
  routes
})

export default router
