<template>
  <div class="nav-common">
    <header>
      <el-menu
        :default-active="activeIndex"
        class="el-menu-style"
        mode="horizontal"
        @select="handleSelect"
        background-color="#545c64"
        text-color="#fff"
        active-text-color="#ffd04b"
      >
        <el-menu-item index="home">首页</el-menu-item>
        <el-menu-item index="store">门店中心</el-menu-item>
        <el-menu-item index="3">交易中心</el-menu-item>
        <el-menu-item index="4">用户中心</el-menu-item>
        <el-menu-item index="5">管理员中心</el-menu-item>
      </el-menu>

      <el-dropdown class="user-setting" @command="handleCommand">
        <el-button type="primary">
          {{userName}}
          <i class="el-icon-arrow-down el-icon--right"></i>
        </el-button>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item>更新密码</el-dropdown-item>
          <el-dropdown-item command="loginOut">退出</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
    </header>
  </div>
</template>

<script>
export default {
  // 顶部导航
  name: "navCommon",
  data() {
    return {
      activeIndex: 'home',
      userName: ''
    };
  },
  mounted() {
    this.userName = this.$store.state.user.acc;
  },
  methods: {
    handleSelect(index, indexPath) {
      if(this.activeIndex == index) return
      this.$router.push({
        name: index
      })
      console.log(index, indexPath)
      this.activeIndex = index
    },
    async handleCommand(command) {
      try {
        switch (command) {
          case "loginOut":
            await this.$confirm("是否确定退出登录", "提示", {
              confirmButtonText: "确定",
              cancelButtonText: "取消",
              type: "warning"
            });
            this.$store.commit("saveUser", {data: {}});
            this.$router.push({
              name: "login"
            });
            break;

          default:
            break;
        }
      } catch (error) {
        if (error == "cancel") return;
      }
    }
  }
};
</script>

<style scoped>
.nav-common {
  position: fixed;
  width: 100%;
  background-color: #545c64;
  z-index: 10;
}
header {
  box-sizing: border-box;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  height: 60px;
  padding-right: 30px;
  padding-left: 200px;
}
.el-menu-style {
  box-sizing: border-box;
  width: 100%;
  padding: 0 40px;
}
</style>
