<template>
  <div class="login">
    <div class="login-form-wrap">
      <el-form
        :model="form"
        :rules="rules"
        ref="form"
        class="login-form"
        label-width="80px"
        label-position="left"
      >
        <el-form-item label="账号" prop="acc">
          <el-input v-model="form.acc"></el-input>
        </el-form-item>
        <el-form-item label="密码" prop="pass">
          <el-input type="password" v-model="form.pass"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitForm()">登入</el-button>
          <el-button @click="resetForm()">重置</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
export default {
  name: "login",
  data() {
    return {
      imgs: [
        {url: '@/assets/rainbow6.jpg'},
        {url: '@/assets/灌篮高手.jpg'},
      ],
      form: {
        acc: "",
        pass: ""
      },
      rules: {
        acc: [{ required: true, message: "请输入账号" }],
        pass: [{ required: true, message: "请输入密码" }]
      }
    };
  },
  methods: {
    async submitForm() {
      try {
        await this.$refs["form"].validate();
        this.$store.commit("saveUser", { data: this.form });
        this.$message.success(`欢迎${this.form.acc}!`);
        this.$router.push({
          name: "home"
        });
      } catch (error) {
        this.$message.error("登入出错！");
      }
    },
    resetForm() {
      this.$refs["form"].resetFields();
    }
  }
};
</script>

<style scoped>
.login {
  display: flex;
  align-items: center;
  justify-content: center;
}
.login-form-wrap {
  position: absolute;
  top: 30%;
  left: 50%;
  width: 400px;
  margin-left: -200px;
}
</style>
