<template>
  <div class="left-common">
    <el-menu
      :default-active="activeIndex"
      class="el-menu-vertical"
      @select="handleSelect"
      :collapse="false"
    >
      <el-menu-item v-for="(item, index) in listData" :key="index" :index="item.index">
        <i :class="item.class"></i>
        <span slot="title">{{item.title}}</span>
      </el-menu-item>
    </el-menu>
  </div>
</template>

<script>
export default {
  // 左边栏导航
  name: "store",
  props: {
    listData: Array
  },
  data() {
    return {
      activeIndex: this.listData[0].title
    };
  },
  methods: {
    handleSelect(index, indexPath) {
      if(this.activeIndex == index) return
      this.$router.push({
        name: index
      })
      console.log(index, indexPath)
      this.activeIndex = index
    },
  }
};
</script>

<style scoped>
.left-common {
  display: flex;
  width: 230px;
  height: 100%;
}
.el-menu-vertical{
  width: 230px;
  text-align: left;
}
</style>
